import { isTagEmbed, isTagShop, isSocialWall } from "../../../../actions/api";
import { CloudUrl, WebWidgetUrl } from "../../../../constants";

const ErrorBranding = ({ isLink }) => {
  const logoName = isTagEmbed ? 'tagembed.svg' : isTagShop ? 'tagshop.svg' : isSocialWall ? 'socialwalls.svg' : 'tagbox-widget.svg'
  return <div className="tb_error_page_logo">
    <a href={isLink ? isLink : `${WebWidgetUrl}`} target={'_blank'} className="tb_error_page_logo_ico" style={{
      backgroundImage: `url(${CloudUrl}/media/images/logo/${logoName})`
    }}> </a>
  </div>
}
export default ErrorBranding
